.box{
    position: relative;
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px;
    background: #060c21;
    transition: 0.4s;
}

.box:hover{
    height: 380px;
}

.box .imgBox{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.box .imgBox img{
    max-width: 100%;
    /* height: 400px; */
    opacity: 0.1;
    transition: 0.5s;
}

.box:hover .imgBox img{
    opacity: 1;
}

.box:before{
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background:#fff ;
    z-index: -1;

}

.box:after{
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background:#fff ;
    z-index: -2;
    filter: blur(10px);
}
