*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'montserrat',sans-serif ;
  background: #223343;
}

header{
  width: 50%;
  margin: 0 auto;
  padding: 15px 15px 0;
}

h1{
  color: #fff;
  font-size: 38px;
  font-weight: 800;
  text-align: center;
  background: rgba(34, 34, 34, 0.541);
  border-radius: 0 0 80px 80px;
  margin-top: -15px;
  margin-bottom: 50px;
  padding: 20px;
  box-shadow: 2px 10px 12px rgba(0, 0, 0, 0.479);
}


main{
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

section{
  padding: 15px;
}

.searchbox{
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background: none;

  background-color: #eee;
  border-radius: 15px 15px 0 0 ;

  color: #53565A;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
    box-shadow: 2px 10px 12px rgba(0, 0, 0, 0.479);

}

.searchbox:focus{
  box-shadow: 0 0 8px 3px #4484C4;
}

.results{
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  justify-content: center;
  transition: 0.4s;
}
.results .result{
  width: 33,333%;
  min-width: 100px;
  padding: 15px;
  display: flex; 
  flex-direction: column;
  box-shadow: 10px 10px 8px black;
}

.results .result img{
  display: block;
  width: 100%;
  height: 400px;

}

.results .result h3{
color: #fff;
text-align: center;
font-size: 20px;
font-weight: 600;
padding: 10px;
background:#445565;
flex: 1 1 100%;
transition: 0.4s ease-out;
}

.results .result:hover h3{
background: #fff;
color: #223343;
}

.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
   background-color: #445565;

   overflow-y:scroll;
}

.popup .content{
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;

  color: #fff;
}

.popup .content h2{
  font-size: 28px;
  font-weight: 600;
}


.popup .content span{
  font-size: 20px;
  font-weight: 300;
}


.popup .content .rating{
margin-bottom: 30px;
}

.popup .content .plot{
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px 30px;

}

.popup .content .plot img{
  flex: 1 1 50%;
  max-width: 300px;
  padding: 0 15px;

}

.popup .content .plot p{
  flex: 1 1 50%;
  padding: 15px;
}

.popup .content .close{
  display: inline-block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: 700;

  background: #4484C4;
  color: #fff;
  border-radius: 8px;
  border: none;
  outline: none;
  appearance: none;
  cursor: pointer;
  transition: 0.4s ease-out;
}

.popup .content .close:hover{
  background :#223343  ;
}